.product-heading{
    text-align: center;
    padding: 4rem 0 2rem 0;
}

.product-container{
    max-width: 1140px;
    margin: auto;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-gap: 40px;
    padding-bottom: 5rem;
}

.product-card{
    background: #1a1919;
    padding: 1.2rem 1rem;
}

.product-card img{
    width: 100%;
}

.product-title{
    color: #fff;
    padding: 1rem 0;
}

.pro-details p{
padding-bottom: 1rem;
font-size: 1.1rem;
text-align: justify;
}

@media screen and (max-width: 740px) {
    .product-container {
        max-width: 90%;
        margin: auto;
        grid-template-columns: 1fr;
        }
}